function catalogSliders() {
    let SlidersArray = []
    $(".catalog-card__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.catalog-card__slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            loop: true,
            navigation: {
                nextEl: `.catalog-card__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.catalog-card__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    loop: false,
                },
                992: {
                    loop: true,
                }
            }
        })
    })
}

function gallerySliders() {
    let SlidersArray = []
    $(".gallery-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.gallery-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: true,
            navigation: {
                nextEl: `.gallery-slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery-slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.gallery-slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'fraction',
            }
        })
    })
}

function sliderPartners() {
    var swiper = new Swiper('.partners .swiper', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.partners .swiper-button-next',
            prevEl: '.partners .swiper-button-prev',
        },
    })
}

catalogSliders()
gallerySliders()
sliderPartners()
