let mapBlock = document.getElementById("map")

if(mapBlock){
    ymaps.ready(init);
}

function init() {
    let coord = [55.724170, 37.280893]
    let mapPoints = [
        {
            title: "г. Москва, Каширское шоссе дом 3 корпус 2, строение 2",
            workTime: `Понедельник-воскресенье: <br>8:00–21:00`,
            cords: [55.673392, 37.632475]
        },
    ]
    let myMap = new ymaps.Map("map", {
        center: coord,
        zoom: 15,
        controls: []
    });



    let placeMark = new ymaps.Placemark(coord, {},
        {
            iconLayout: 'default#image',
            iconImageHref: "img/mark.svg",
            iconImageSize: [78, 93],
        }
    );
    myMap.geoObjects.add(placeMark);
}

