$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$(window).scroll(function(){
    setFixHeader()
});


setFixHeader()


function setFixHeader() {
    var st = $(this).scrollTop();
    if(st > 100){
        $(".header").addClass("_fixed-header")
    }else {
        $(".header").removeClass("_fixed-header")
    }
}
